@import url(https://fonts.googleapis.com/css?family=Playfair+Display:700|Source+Sans+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hideScrollbar{overflow:hidden !important}.myName{font-family:"Quicksand", Arial, sans-serif;font-size:1.65rem !important}

.crop{display:block;margin:8% auto 8%;color:black}.framework-skill-card{background:#779ecb;border-color:#779ecb;border-radius:15px 50px 30px}.programing-skill-card{background:#40B5BC;border-color:#40B5BC;border-radius:15px 50px 30px}.tool-skill-card{background:#42ABC5;border-color:#42ABC5;border-radius:15px 50px 30px}.section-skills{background-color:whitesmoke;background-size:cover}.card-divider{margin:auto;border-top:3px solid #aec6cf;width:80%}h5{text-align:center;padding-bottom:3%;color:white}.padding-deck{padding-left:2%;padding-right:2%;color:white}.card-text{text-align:center;margin:1%}

.section-about{background-color:whitesmoke;background-size:cover}.body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Source Sans Pro', sans-serif;font-size:16px;line-height:1.6}.title{text-align:center;font-weight:bold;font-size:35px}.subtitle{text-align:center;font-weight:bold;font-size:20px}.introduce{text-align:center;font-size:20px}

h1{color:black;margin-bottom:3%}.code-button{position:absolute;right:0;bottom:8px;background-color:black;border:1px solid;height:42px;border-radius:50%;-moz-border-radius:50%;-webkit-border-radius:50%;width:42px;-webkit-align-content:center;align-content:center}.project-text{text-align:left;margin:1%}.project-title{text-align:center;margin:1%}

h3{font-family:"Quicksand", Arial, sans-serif;font-weight:bold;line-height:1.2;margin:1rem;letter-spacing:1px;font-size:4.3rem;text-align:center;color:black}.section-projects{background:#aec6cf}.card-container{padding:2%}

